import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { Routes, RouterModule } from '@angular/router';
import { ProfesorShellComponent } from './profesor-shell/profesor-shell.component';
import { ProfesorEdicionComponent } from './profesor-edicion/profesor-edicion.component';
import { ProfesorListaComponent } from './profesor-lista/profesor-lista.component';
import { ProfesorNuevoComponent } from './profesor-nuevo/profesor-nuevo.component';

const routes: Routes = [
  { path: '', component: ProfesorShellComponent, children: [
      { path: '', redirectTo: 'lista'},
      { path: 'lista', component: ProfesorListaComponent},
      { path: 'nuevo', component: ProfesorNuevoComponent },
      { path: 'modificar/:id', component: ProfesorEdicionComponent}
    ]
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class ProfesorRoutingModule { }
