import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-profesor-shell',
  templateUrl: './profesor-shell.component.html',
  styleUrls: ['./profesor-shell.component.css']
})
export class ProfesorShellComponent implements OnInit {

  public isCollapsed: boolean = false;

  constructor() { }

  ngOnInit() {
  }

  options = [
    {
      icon: 'file-search',
      title: 'Profesores',
      url: '../admin/profesor/lista'
    },
    {
      icon: 'file-add',
      title: 'Eventos',
      url: 'evento'
    },
    {
      icon: 'file-add',
      title: 'Personal',
      url: 'personas'
    },
    {
      icon: 'file-add',
      title: 'Reportes',
      url: 'reportes'
    },
  ];

  public handleCollapse() {
    window.innerWidth < 768 ? this.isCollapsed = true : null;
  }

  public collapseChange() {
    window.innerWidth < 768 ? this.isCollapsed = false : null;
  }

}
